.hero {
    width: 100vw;
    height: 550px;
    /*background-image:linear-gradient(to bottom,#e66465, #9198e5), url('.././assets/hero.jpg');*/
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-image:linear-gradient(to bottom, rgba(68, 68, 70, 0.52), rgba(7, 7, 7, 0.73)), url('../assets/hero.jpg');

    display: flex;
    flex-direction: column;
    justify-content: center;
}

#hero-primary-text {
    width: 615px;
    margin-left: 60px;
    color: white;
}

#hero-text {
    width: 615px;
    margin-left: 60px;
    color: white;
    margin-top: 20px;
}

#hero-button {
    margin-top: 20px;
    margin-left: 60px;
}
