:root {
    --primarycolour: #F4AD1B;
    --secondarycolour: #E7563E;
    --tertiarycolour: #224E80;
    --primary-font-size: 60px;
    --body-text-size: 20px;
}

.primaryfont {
    font-size: var(--primary-font-size);
}

.body-text {
    font-size: var(--body-text-size);
}

.button-shadow {
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.button-1 {
    border: solid var(--tertiarycolour) 2px;
    background: var(--tertiarycolour);
    color: white;
    width: 150px;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
}

.button-2 {
    border: solid var(--secondarycolour) 2px;
    background: var(--secondarycolour);
    color: white;
    width: 150px;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
}