* {
    margin: 0;
    padding: 0;
}

.sml-logo {
    width: 50px;
}

.very-left{
    padding-right: 150px;
}
nav {
    font-size: 16px;
    height: 72px;
    background: linear-gradient(0.25turn, #ca8f18, var(--primarycolour));
}

ul {
    display: flex;
    justify-content: space-evenly;
    height: 72px;
    align-items: center;
    
}

li {
    list-style-type: none;

}

a {
    text-decoration: none;
}

a:visited {
    color: inherit;
}

a:hover{
    color: var(--secondarycolour);
    transition: 0.2s;
}

.button-shadow {
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.button {
    border: solid var(--tertiarycolour) 2px;
    background: var(--tertiarycolour);
    color: white;
    width: 150px;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
}



